body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

body {
  background-color: #121526; }

a {
  text-decoration: none;
  color: #eee; }

a:visited {
  text-decoration: none; }

#main {
  padding: 10%;
  background-color: #121526; }

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%; }

#quote-box {
  height: auto;
  width: 80%;
  border: solid; }

#author {
  padding-left: 70%;
  font-family: 'Raleway', sans-serif;
  font-size: 2vw;
  padding-bottom: 5%;
  font-display: fallback; }

#text {
  margin: 5%;
  font-family: 'Alfa Slab One', cursive;
  font-size: 3vw; }

#tweet-quote {
  height: 4vw;
  min-height: 30px;
  width: 10vw;
  min-width: 80px;
  text-align: center;
  vertical-align: center;
  margin-top: 5%; }

#new-quote {
  height: 4vw;
  min-height: 30px;
  width: 10vw;
  min-width: 80px;
  border-radius: 10px;
  margin-top: 5%; }

#new-quote:hover {
  cursor: pointer; }

.fa-twitter {
  font-size: 5vw;
  color: #38A1F3; }

